<template>
  <div class="container clearfix">
    <div class="container-box flex-box">
      <div class="left">
        <div class="header flex-box margin-20">
          <div class="item logo tc">
            <svg-icon icon-class="qiu" class="fz-36"></svg-icon>
          </div>
          <div class="item menu">
            <ul>
              <li class="active">Dashoboard</li>
              <li class>Dashoboard</li>
              <li class>Dashoboard</li>
              <li class>Dashoboard</li>
            </ul>
          </div>
          <div class="item search">
            <input class="search" placeholder="搜索" />
            <span class>
              <svg-icon icon-class="sousuo"></svg-icon>
            </span>
          </div>
        </div>
        <div class="cards-box margin-20">
          <div class="cards-header padding-20">
            <div class="clearfix">
              <span class="active">Cards</span>
              <strong class="fr">View all</strong>
            </div>
          </div>
          <div class="cards-body flex-box pl-20 pr-20">
            <card-item></card-item>
          </div>
        </div>
        <div class="flex-box margin-20">
          <div class="service">
            <div class="service-header padding-20">
              <div class="clearfix">
                <span class="active">Main Service</span>
                <strong class="fr">View all</strong>
              </div>
            </div>
            <div class="service-body clearfix pl-20 pr-20">
              <div class="panel p-r" v-for="(item,index) in data" :key="index">
                <div class="center-box">
                  <div class="tc">
                    <svg-icon icon-class="deng" class="fz-36 tc"></svg-icon>
                    <p class="tc mt-5">Tfdgdfg</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="form">
            <div class="Current">
              <div class="current-header padding-20">
                <div class="clearfix">
                  <span class="active">Current</span>
                  <strong class="fr">View all</strong>
                </div>
              </div>
              <div class="current-body pl-20 pr-20">
                <ul>
                  <li
                    class="clearfix mt-10 mb-10"
                    v-for="(item ,index) in currentdata"
                    :key="index"
                  >
                    <span>18888888</span>
                    <strong class="fr">$563333</strong>
                  </li>
                </ul>
              </div>
            </div>
            <div class="Savings">
              <div class="savings-header padding-20">
                <div class="clearfix">
                  <span class="active">savings</span>
                  <strong class="fr">View all</strong>
                </div>
              </div>
              <div class="savings-body pl-20 pr-20">
                <div>
                  <div style="width:100%;height:100%" ref="chart_line"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="personal">
          <div class="personal-header padding-20">
            <div class="clearfix">
              <svg-icon icon-class="ling" class="tc fz-20 mt-10"></svg-icon>

              <svg-icon icon-class="ling" class="fz-36 tc fr"></svg-icon>
              <span class="active fr mt-10 mr-10">Current</span>
            </div>
          </div>
          <div class="personal-body padding-20">
            <div class="chart-pie">
              <div style="width:100%;height:100%" ref="chart_pie"></div>
            </div>
          </div>
        </div>
        <div class="transaction padding-20">
          <div class="transaction-header">
            <div class="clearfix">
              <span class="active">Transaction</span>
              <strong class="fr">View all</strong>
            </div>
          </div>
          <div class="transaction-body">
            <ul>
              <li class="clearfix mt-10 mb-10" v-for="(item ,index) in currentdata" :key="index">
                <svg-icon icon-class="deng" class="fz-36 tc fl"></svg-icon>
                <span class="fl mt-10">18888888</span>
                <strong class="fr mt-10">$563333</strong>
              </li>
            </ul>
            <div class="btn-box padding-10 margin-20">
              <div class="clearfix">
                <span class="active">Transaction</span>
                <svg-icon icon-class="you" class="fz-20 fr"></svg-icon>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardItem from "./card";
import echarts from "echarts";
export default {
  name: "",
  components: { CardItem },
  data() {
    return {
      data: 6,
      currentdata: 3,
      msg: "Welcome"
    };
  },
  mounted() {
    this.initCharts();
  },
  methods: {
    initCharts() {
      let chart_line = echarts.init(this.$refs.chart_line);
      var option_line = {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["Total"]
        },
        grid: {
          top: "10%",
          left: "3%",
          right: "3%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["$8522", "8522", "8522", "8522", "8522", "8522"]
        },
        yAxis: {
          min: 0,
          max: 100,
          interval: 20,
          axisLabel: {
            formatter: "{value}%"
          },
          type: "value"
        },
        series: [
          {
            name: "",
            type: "line",
            data: [20, 32, 100, 34, 90, 30, 10]
          }
        ]
      };
      // 绘制图表
      chart_line.setOption(option_line);

      let chart_pie = echarts.init(this.$refs.chart_pie);
      var option_pie = {
        color: ["#FF2934", "#0E809A", "#F8F8F8"], //环形颜色
        series: [
          {
            name: "",
            type: "pie",
            center: ["50%", "50%"], // 饼图的圆心坐标
            radius: ["100%", "80%"],
            avoidLabelOverlap: false,
            hoverAnimation: false,
            label: {
              //  饼图图形上的文本标签s
              normal: {
                // normal 是图形在默认状态下的样式
                show: true,
                position: "center",
                color: "red",
                fontSize: 18,
                fontWeight: "bold",
                formatter: "{d}%\n{b}" // {b}:数据名； {c}：数据值； {d}：百分比，可以自定义显示内容，
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: 50,
                name: "hfgh",
                label: {
                  normal: {
                    show: true
                  }
                }
              },
              {
                value: 20,
                name: "正常",
                label: {
                  normal: {
                    show: false
                  }
                }
              }
            ]
          }
        ]
      };
      // 绘制图表
      chart_pie.setOption(option_pie);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang='sass'>
.container
  background: #fdebea
  &-box
    position: absolute
    left: 50%
    top: 50%
    transform: translate(-50%, -50%)
    background: #fdfbfb
    border: 5px solid #fddada
    border-radius: 30px
    width: 80%
    min-width: 1024px
.left
  flex: 1
.left .header
  position: relative
  display: flex
  display: -webkit-flex
  justify-content: space-around
  align-items: center
  & .logo
    flex: 1
  & .menu
    flex: 5
  & .menu ul li
    float: left
    margin: 10px
  & .search
    flex: 2
    position: relative
  & .search input
    width: 100%
    height: 30px
    padding: 8px 20px 8px 20px
    border-radius: 15px
    border: none
    background-color: #f2f2f2
  & .search span
    position: absolute
    right: 20px
    top: 8px
.left .service
  min-width: 250px
  max-width: 500px
  & .service-body
  & .service-body .panel
    float: left
    width: 46%
    min-height: 100px
    margin: 2%
    background-color: #fff
    border-radius: 15px
    border: 1px solid #fddada

.left .form
  flex: 1
.right
  width: 300px
  background-color: #fff
  border-radius: 30px
  box-shadow: 0 0 15px #f4f6f7
.right .personal
.right .personal-body .chart-pie
.right .transaction
  & .transaction-body .btn-box
    border-radius: 15px
    background-color: #FF4D56
</style>



