<template>
  <div class="card-box">
    <div class="item" v-for="item in list" :key="item.id" :style="item.color">
      <ul>
        <li class="type margin-10">{{item.type}}</li>
        <li class="money margin-10">$ {{item.money}}</li>
        <li class="number pt-5 pb-5 margin-10">{{item.number}}</li>
        <li class="name margin-10">
          <div class="clearfx">
            <span>{{item.name}}</span>
            <strong class="fr">
              <div class="circle"></div>
            </strong>
          </div>
        </li>
      </ul>
    </div>
    <div class="item up-file">
      <div class="center-box">
        <div class="p-r pointer">
          <input
            id="upload-input"
            class="hide-input percent-100"
            type="file"
            accept="image/gif, image/jpg, image/png"
          />
          <svg-icon icon-class="add" class="fz-36"></svg-icon>
          <p class="tc">add</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      chart: null,
      list: [
        {
          type: "Adgffgdfg dsfgdfg",
          money: "56,322,222",
          number: "8888 8569 5632 9359",
          name: "luo oll",
          color: {
            "background-color": "#FF4D56"
          },
          id: 1
        },
        {
          type: "sdgd rfgrfg",
          money: "5666,222",
          number: "6666 8569 5632 9359",
          name: "dsfg oll",
          color: {
            "background-color": "#0E809A"
          },
          id: 2
        }
      ]
    };
  },
  mounted() {},
  beforeDestroy() {},
  created() {},
  methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='sass'>
.card-box 
    position: relative
    display: flex
    display: -webkit-flex
    justify-content: space-around
    align-content: center
    width: 100%
    & .item
        border-radius: 15px
        min-width: 32%
        margin-right: 1%
    
    & .up-file
        position: relative
        border-radius: 15px
        background-color: #f8f8f8
</style>



